const ns = {
  NAME: "UU5.Common",
  CSS: "uu5-common",
  name(name) {
    return ns.NAME + "." + name;
  },
  css() {
    return Array.prototype.slice.call(arguments).map(name => (ns.CSS + "-" + name)).join(" ");
  }
};

export default ns;



