import createReactClass from 'create-react-class';

export default (cmp) => {
  let reactCmp = createReactClass(cmp);
  // fix warning
  if(reactCmp.getDefaultProps) {
    reactCmp.getDefaultProps.isReactClassApproved = true;
  }
  return reactCmp
}



